    export default{
        data(){
            return { 
                openSettingBtnHidden: true,//是否授权
				xiazai: require("../../static/img/all/mine/xiazai.png"),
                ewmImg:"http://img0.imgtn.bdimg.com/it/u=3907776734,3838282988&fm=11&gp=0.jpg"//这是要保存的图片
            }
        },
        onLoad(opt) { 
        },
        components:{
        },
        methods:{
            //微信小程序保存到相册
            handleSetting(e){
				let _this=this;
                if (!e.detail.authSetting['scope.writePhotosAlbum']) {
                      _this.openSettingBtnHidden = false;
                } else {
                     _this.openSettingBtnHidden = true;
                }
            },
            saveEwm:function(e){
				let _this=this;
                //获取相册授权
                   uni.getSetting({
                     success(res) {
                       if (!res.authSetting['scope.writePhotosAlbum']) {
                         uni.authorize({
                           scope: 'scope.writePhotosAlbum',
                           success() {
                             //这里是用户同意授权后的回调
                             _this.saveImgToLocal();
                           },
                           fail() {//这里是用户拒绝授权后的回调
                               _this.openSettingBtnHidden=false
                           }
                         })
                       } else {//用户已经授权过了            
                         _this.saveImgToLocal();
                       }
                     }
                   })
            },
            saveImgToLocal:function(e){
				
				 let _this=this;
                
                uni.showModal({
                    title: '提示',
                    content: '确定保存到相册吗',
                    success: function (res) {
                        if (res.confirm) {
                           uni.downloadFile({
                                   url: _this.ewmImg,//图片地址
                                   success: (res) =>{
                                       if (res.statusCode === 200){
                                           uni.saveImageToPhotosAlbum({
                                               filePath: res.tempFilePath,
                                               success: function() { 
                                                   uni.showToast({
                                                       title: "保存成功",
                                                       icon: "none"
                                                   });
                                               },
                                               fail: function() { 
                                                   uni.showToast({
                                                       title: "保存失败",
                                                       icon: "none"
                                                   });
                                               }
                                           });
                                       } 
                                   }
                               }) 
                            
                            
                            
                        } else if (res.cancel) {
                            
                        }
                    }
                });
                
            }
        }
    }
